import { useEffect, useState, useCallback } from 'react';
import { FieldTitles, Dividers, Cautions, FloatButtons, InputTexts, InputTextAreas } from '../../styles/Common';
import ReviewRequestForms from './Style';
import ReviewRequestModal from '../ReviewRequestModal';
import ScheduleList from '../ScheduleList';
import scheduleData from '../../tempData/scheduleData';
import useInputText from '../../hooks/useInputText';
import useInputCheck from '../../hooks/useInputCheck';

function ReviewRequestForm() {
    //   
    const [checkSchedules, handlerSchedule] = useInputCheck();

    //  
    const [link, onChangeLink] = useInputText('');

    //    
    // const [linkError, setLinkError] = useState(true);

    //    
    const [linkErrorModal, setLinkErrorModal] = useState(false);
    const onCloseModal = useCallback(() => {
        setLinkErrorModal(false);
    }, []);

    //   
    const [message, onChangeMessage] = useInputText('');

    //    
    const [canSubmit, setCanSubmit] = useState(false);
    useEffect(() => {
        if (checkSchedules.length < 1) return;
        if (link.length < 1) return;
        if (message.length < 50) return;
        
        setCanSubmit(true);

        return () => {
            setCanSubmit(false);
        }
    }, [checkSchedules, link, message]);

    // 
    const onSubmitRequest = useCallback((e) => {
        e.preventDefault();
        // if (linkError) return setLinkErrorModal(true); //      
        setLinkErrorModal(true); // QA      

        let scheduleId = '';
        for (let x of checkSchedules) {
            scheduleId += `${x},`;
        }

        console.log(`  id: ${scheduleId}\n : ${link}\n  : ${message}`);
    }, [checkSchedules, link, message]);

    return (
        <ReviewRequestForms onSubmit={onSubmitRequest}>
            <article className='form-schedule'>
                <FieldTitles>
                    <div className='title'>
                        <h6> </h6>
                        <small>    </small>
                    </div>
                </FieldTitles>
                <ScheduleList name={'schedule'} handler={handlerSchedule} data={scheduleData} checkIds={checkSchedules} />
            </article>
            <Dividers/>
            <article className='form-link'>
                <FieldTitles>
                    <div className='title'>
                        <h6> </h6>
                        <small></small>
                    </div>
                    <div className='text'>
                        <p>     .</p>
                    </div>
                </FieldTitles>
                <div className='form-link-input'>
                    <InputTexts type='text' value={link} onChange={onChangeLink} placeholder={'   .'} />
                </div>
                <Cautions>
                    <p>PDF      ,      .</p>
                </Cautions>
            </article>
            <Dividers/>
            <article className='form-message'>
                <FieldTitles>
                    <div className='title'>
                        <h6>  </h6>
                        <small> ( 500)</small>
                    </div>
                </FieldTitles>
                <div className='form-message-input'>
                    <InputTextAreas value={message} onChange={onChangeMessage} maxLength='500' placeholder={'           . ( 50)'}></InputTextAreas>
                </div>
            </article>
            <FloatButtons type='submit' disabled={!canSubmit}></FloatButtons>
            {linkErrorModal && <ReviewRequestModal title={'  '} text={'  .    .'} onClose={onCloseModal} />}
        </ReviewRequestForms>
    );
}

export default ReviewRequestForm;